import React from 'react'
import { Link } from "react-router-dom";
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBook } from '@fortawesome/free-solid-svg-icons';
import { faTwitter, faDiscord, faMediumM } from '@fortawesome/free-brands-svg-icons';

export default function Footer() {

  const { language } = useApp();
  const text = textContext[language].navbar;

  const socials = [
    { icon: <FontAwesomeIcon icon={faDiscord} />, link: 'https://discord.com/invite/MGHguks25G' },
    { icon: <FontAwesomeIcon icon={faTwitter} />, link: 'https://twitter.com/ApertureFinance' },
    { icon: <FontAwesomeIcon icon={faMediumM} />, link: 'https://medium.com/@aperturefinance' },
    { icon: <FontAwesomeIcon icon={faBook} />, link: 'https://docs.aperture.finance/docs/' },
  ]

  return (
    <footer>
      <div className="footerLink">
        {
          text.footer.links.map((l, i) => (
            <div key={i}>
              <Link className="linkClass" to={`/${l.link}`}>
                <p className="textSecondary">{l.text}</p>
              </Link>
            </div>
          ))
        }
      </div>
      <div className="footerSocial">
        {
          socials.map((s, i) => (
            <div key={i}>
              <a href={s.link} target="_blank" rel="noreferrer">
                {s.icon}
              </a>
            </div>
          ))
        }
      </div>
    </footer>
  )
}