import React from 'react'
import { useDelta } from '../../context/DeltaContext';
import { useTheme } from '../../context/ThemeContext';
import { useToken } from '../../context/TokenContext';
import LinearProgress from '@mui/material/LinearProgress';
import styled from 'styled-components';

const BorderLinearProgress = styled(LinearProgress)`
  width: 100%;
  height: 28px;
  opacity: .1;
  border-radius: 8px;
  position: relative;
  top: -3px;
`;

const Investment = (props) => {

  const { position } = props;
  const { parseNum } = useToken();

  const { palette } = useTheme();
  const assetImg = position.name && (
    palette.name === 'light'
    ? `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons/${position.name.substring(1)}.png`
    : `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons_dark/${position.name.substring(1)}.png`
  );

  const handleSelect = () => {
    props.handleSelect({ position: position.position, title: `delta-neutral-${position.name}` })
  };

  return(
    <div onClick={handleSelect} className="appInvest">
      {
        !position.name ? <BorderLinearProgress color="inherit" /> :
        <React.Fragment>
          <div className="flex">
            <img src={assetImg} alt="delta-neutral ust" />
            <div>
              <p>Delta-neutral-{position.name && position.name}</p>
              <span>Terra</span>
            </div>
          </div>
          <p>${position.value && parseNum(position.value)}</p>
        </React.Fragment>
      }
    </div>
  )
}

export default function ApertureInvestments(props) {

  const { positionInfo } = useDelta();

  return (
    <div>
      {
        positionInfo.length !== 0 && positionInfo.map((d,i) => {
          if (!d.closed) {
            return <Investment handleSelect={props.handleSelect} key={i} position={d} />
          }
          else {
            return null
          }
        })
      }
    </div>
  )
}