import React from 'react'
import { useDelta } from '../../context/DeltaContext';

// Comps
import Trade from './Trade'

export default function Trades(props) {

  const { positionInfo, anchorInvestment } = useDelta();

  const sortNum = (a,b) => {
    return b.position - a.position;
  };

  const handleSelect = (select) => {
    props.handleSelect(select);
  }
  
  return (
    <div id="trades">
      {
        [anchorInvestment].map((d,i) => (
          <Trade handleSelect={handleSelect} key={i} anchor={d.value} selected={props.selected === 'anc'} position={d} />
        ))
      }
      {
        positionInfo.length !== 0 && positionInfo.sort(sortNum).map((d,i) => (
          !d.closed && <Trade handleSelect={handleSelect} key={i} selected={d.position === props.selected} position={d} />
        ))
      }
    </div>
  )
}