import React from 'react'
import { ust, luna, anchor, aUST, spec, mir } from '../../assets/images/icons';
import { useToken } from '../../context/TokenContext';
import { useDelta } from '../../context/DeltaContext';

const Holdings = (props) => {

  const { balance } = useToken();

  const { parseNum } = useToken();
  const handleImg = (img) => {
    switch (img) {
      case 'ust':
        return ust;
      case 'uluna':
        return luna;
      case 'anc':
        return anchor;
      case 'aust':
        return aUST;
      case 'spec':
        return spec;
      case 'mir':
        return mir;
      default:
        return null;
    }
  }

  return(
    <div className={`appInvest token ${props.anchor ? 'anchorInvestClass' : ''}`}>
      <div>
        <div className="flex">
          <img src={handleImg(props.token)} alt="token" />
          <p>{props.token === 'uluna' ? 'luna' : props.token}</p>
        </div>
      </div>
      <div>
        {
          props.token === 'ust' ? 
          <p>{parseNum(balance)}</p>
          :
          <p>~ {parseNum(props.amount/1000000)} UST</p>
        }
      </div>
    </div>
  )
}

export default function TokenHoldings(props) {

  const { balances, coinBalances } = useDelta();
  const [balanceList, setBalance] = React.useState([])
  const [coinList, setCoins] = React.useState([])

  React.useEffect(() => {
    if (balances.length === 5 && coinBalances.length !== 0) {
      setBalance(balances);
      setCoins(coinBalances);
    }
  }, [balances, coinBalances])

  return (
    <div className="tokenHoldings">
      {
        balanceList.filter(f => Number(f.amount/1000000) > 0.01).map((l,i) =>
          <Holdings key={i} img={l.img} token={l.token} amount={l.amount} anchor={l.anchor} /> )
      }
      {
        coinList.filter(f => Number(f.amount/1000000) > 0.01).map((l,i) =>
          <Holdings key={i} img={l.img} token={l.token} amount={l.amount} anchor={l.anchor} /> )
      }
    </div>
  )
}