import React from 'react'
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import { useApp } from '../../context/AppContext';
import textContext from '../../textContext';

export default function Dropdown(props) {

  const { language } = useApp();
  const text = textContext[language].investment.invest.dropdown;

  return (
    <div onClick={(event) => event.stopPropagation()} className="accordionDrop">
      {
        props.title === 'Anchor Protocol' ?
        <React.Fragment>
          <p style={{width:'auto', paddingTop:'.5rem'}}>{text.anchorText1}</p>
          <p style={{width:'auto', paddingTop:'.5rem'}}>{text.anchorText2}</p>  
        </React.Fragment>
        :
        <p style={{width:'auto'}}>{text.text1} <strong>{props.title} ({props.name})</strong> {text.text2}</p>
      } 
      <div className="flex" style={{flexWrap: 'wrap'}}>
        <div className="cont">
          <span>{text.protocols}</span>
            {
              props.title === 'Anchor Protocol' ?
              <p>{text.anchor}</p> :
              <React.Fragment>
                <p>{text.anchor}</p>
                <p>{text.mirror}</p>
                <p>{text.spectrum}</p>
              </React.Fragment>
            }
        </div>
        <div className="cont">
          <span>{text.code}</span>
            {
              props.title === 'Anchor Protocol' ?
              <p><a target="_blank" rel="noreferrer" href={text.open.anchor}>{text.open.text}</a></p> :
              <React.Fragment>
                <p><a target="_blank" rel="noreferrer" href={text.open.anchor}>{text.open.text}</a></p>
                <p><a target="_blank" rel="noreferrer" href={text.open.mirror}>{text.open.text}</a></p>
                <p><a target="_blank" rel="noreferrer" href={text.open.spectrum}>{text.open.text}</a></p>
              </React.Fragment>
            }
        </div>
        <div className="cont">
          <span>{text.security}</span>
            {
              props.title === 'Anchor Protocol' ?
              <p><a target="_blank" rel="noreferrer" href={text.completed.anchor}>{text.completed.text}</a></p> :
              <React.Fragment>
                <p><a target="_blank" rel="noreferrer" href={text.completed.anchor}>{text.completed.text}</a></p>
                <p><a target="_blank" rel="noreferrer" href={text.completed.mirror}>{text.completed.text}</a></p>
                <p><a target="_blank" rel="noreferrer" href={text.completed.spectrum}>{text.completed.text}</a></p>
              </React.Fragment>
            }
        </div>
      </div>
      <h4 onClick={props.accordionClose}>Collapse <ExpandLessIcon /></h4>
    </div>
  )
}