import React from 'react'
import { Link, useLocation } from "react-router-dom";
import { useApp } from '../../context/AppContext';
import textContext from '../../textContext';
import { auditImg } from '../../assets/images';

export default function NavLink() {

  const { language } = useApp();
  const text = textContext[language].navbar;

  const { pathname } = useLocation();

  return (
    <div className="navLinks">
      {
        text.navLinks.links.map((nav, i) => (
          <Link className={`linkClass ${(pathname === '/' && i === 0) ? 'active' : ''} ${pathname.includes(nav.text) ? 'active' : ''}`} to={`/${nav.link}`} key={i}>
            <img width="16" src={nav.icon} alt={`${nav.text} icon`} />
            <p>{nav.text}</p>
          </Link>
        ))
      }
      <div className="auditClass">
        <span>{text.navLinks.auditText}</span> <a href="https://www.oaksecurity.io/" target="_blank" rel="noreferrer"><img src={auditImg} alt="Oak Security" /></a>
      </div>
    </div>
  )
}