import React from 'react'
import { useTheme } from '../../context/ThemeContext';
import LightMode from '@mui/icons-material/LightMode';
import DarkMode from '@mui/icons-material/DarkMode';
import styled from 'styled-components';

const Ball = styled('div')`
  background-color: ${props => props.theme.palette.textPrimary};
  position: absolute;
  transition: .25s;
  top: .25rem;
  bottom: .25rem;
  height: 24px;
  width: 24px;
  z-index: 10;
  border-radius: 50%;
`;

export default function Theme() {

  const { palette, themeSelect } = useTheme();

  const handleTheme = () => {
    palette.name === 'dark' ? themeSelect('light') : themeSelect('dark');
  };

  return (
    <div className="themeClass">
      <div className="icons" onClick={handleTheme}>
        <Ball style={palette.name === 'dark' ? {left:'.5rem'} : {left:'3rem'}}/>
        <LightMode />
        <DarkMode />
      </div>
      <div className="text">{ palette.name } mode</div>
      <hr />
    </div>
  )
}