import React from 'react';
import Grid from '@mui/material/Grid';
import Asset from '../../pages/Asset';
import { useNavigate } from 'react-router-dom';
import { anchor, terra } from '../../assets/images/icons';
import { useWallet } from '@terra-money/wallet-provider';
import { useApp } from '../../context/AppContext';
import { useToken } from '../../context/TokenContext';
import { useTheme } from '../../context/ThemeContext';
import Dropdown from './Dropdown';

export default function TableAssets(props) {
  const { width } = useApp();
  const { parseNum } = useToken();
  const { status } = useWallet();
  const [open, setOpen] = React.useState(false);
  const [drop, setDrop] = React.useState(false);

  const { palette } = useTheme();
  const assetImg =
    palette.name === 'light'
      ? `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons/${props.data.title.substring(
          1
        )}.png`
      : `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons_dark/${props.data.title.substring(
          1
        )}.png`;

  const handleClose = () => {
    setOpen(false);
    navigate('/invest');
  };

  const navigate = useNavigate();
  const handleInvest = (e, inv) => {
    e.stopPropagation();
    navigate(`/invest/asset&=${props.data.address}`);
    setOpen(true);
    props.isOpen(true);
  };

  const handleAccordion = () => {
    setDrop(!drop);
    // drop ? props.handleDrop(false) : props.handleDrop(false);
  };

  const accordionClose = () => {
    setDrop(false);
  };

  return (
    <React.Fragment>
      <Grid
        onClick={handleAccordion}
        className={`${drop ? 'active' : ''} tableAssets`}
        container
      >
        <Grid item xs={10} md={6} order={{ xs: 1 }}>
          <span
            className="mobileHeader"
            style={
              props.plus ? { marginLeft: '1.75rem' } : { marginLeft: '3rem' }
            }
          >
            pool
          </span>
          <div className="assetCont">
            <div className="imgContainer">
              {props.plus ? (
                <img src={assetImg} height="35" alt={props.data.token} />
              ) : (
                <div className="imgClass">
                  <img src={terra} height="30" alt={props.data.token} />
                  <img src={anchor} height="30" alt={props.data.token} />
                </div>
              )}
            </div>
            <div>
              <div>
                <p>
                  {props.plus && 'Delta-neutral-'}
                  {props.data.title}
                </p>
                {props.plus && <p className="fontOrange">invest+</p>}
              </div>
              <span className="chainClass">{props.data.chain}</span>
            </div>
          </div>
        </Grid>
        {
          <Grid
            item
            xs={12}
            md={3}
            order={{ xs: 4, md: 2 }}
            className="gridEdit"
            style={!props.plus && width < 900 ? { paddingLeft: '3rem' } : {}}
          >
            <span className="mobileHeader">tvl</span>
            {props.data.tvl
              ? `$${Number(Number(props.data.tvl).toFixed(0)).toLocaleString()}`
              : '-'}
          </Grid>
        }
        <Grid
          item
          xs={12}
          md={1}
          order={{ xs: 3, md: 3 }}
          className="gridEdit"
          style={!props.plus && width < 900 ? { paddingLeft: '3rem' } : {}}
        >
          <span className="mobileHeader">apy</span>
          {props.data.APY && `${parseNum(props.data.APY * 100)}%`}
          {!props.data.APY && `${parseNum(props.data.apy * 100)}%`}
        </Grid>
        <Grid
          className="investButton"
          item
          xs={2}
          md={2}
          order={{ xs: 2, md: 4 }}
        >
          <button
            disabled={status !== 'WALLET_CONNECTED' || !props.data.action}
            className={
              status !== 'WALLET_CONNECTED' || !props.data.action
                ? 'disabled'
                : ''
            }
            onClick={(event) => handleInvest(event, props.data.address)}
          >
            invest
          </button>
        </Grid>
        <Grid item xs={12} order={{ xs: 5 }}>
          {drop && (
            <Dropdown
              name={props.data.name}
              title={props.data.title}
              accordionClose={accordionClose}
              data={props.info}
            />
          )}
        </Grid>
        <Grid xs={12} item order={{ xs: 10 }}>
          <hr className="tableHr" />
        </Grid>
      </Grid>
      {open && (
        <Asset
          invest
          data={props.data}
          img1={terra}
          img2={anchor}
          plus={props.plus}
          token={props.data.address}
          close={handleClose}
        />
      )}
    </React.Fragment>
  );
}
