import React from 'react'
import moment from 'moment';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { anchor, terra, ust } from '../../assets/images/icons';
import { useToken } from '../../context/TokenContext';
import { useDelta } from '../../context/DeltaContext';
import Asset from '../../pages/Asset'
import { bombay12, columbus5, AddressProviderFromJson, } from '@anchor-protocol/anchor.js'
import { useWallet } from '@terra-money/wallet-provider';
import Tooltip from '@mui/material/Tooltip';
import HelpIcon from '@mui/icons-material/Help';
import { useTheme } from '../../context/ThemeContext';

export default function Assets(props) {

  const { anchorInvestment } = useDelta();
  const { network } = useWallet();
  const { holderAddress, exchange, anchorApy, market } = useToken();
  const [openInvest, setOpenInvest] = React.useState(false);
  const [openWithdraw, setOpenWithdraw] = React.useState(false);
  const [investData, setInvestData] = React.useState();
  const [marketHour, setMarket] = React.useState(false);

  const { palette } = useTheme();
  const assetImg = !props.anchor && (
    palette.name === 'light'
    ? `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons/${props.name.substring(1)}.png`
    : `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons_dark/${props.name.substring(1)}.png`
  );
  const { language, getBackendData, isMarketHour, terraClient } = useApp();
  const text = textContext[language].portfolio.asset;

  const handleClose = () => {
    setOpenInvest(false);
    setOpenWithdraw(false);
  };

  const handleInvest = (inv) => {
    inv === 'invest' ?  setOpenInvest(true) : setOpenWithdraw(true);
  };

  const calcApy = (ini, cur) => {
    let momentPositionOpen = moment.unix(props.time / 1e9);
    let momentNow = moment()
    let daysElapsedFloat = momentNow.diff(momentPositionOpen, 'days', true);

    let apy = (Math.pow(cur / ini, 365.0 / daysElapsedFloat) - 1) * 100;
    let investAPY = apy < 0 ? '--' : `${apy.toFixed(2)}%`;
    return investAPY;
  }

  const getTime = (t) => {
    let event = new Date(t/1e6)
    let months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];

    let month = months[event.getMonth()];
    let date = event.getDate();
    let year = event.getFullYear();
    let time = event.toLocaleTimeString('en-us')

    return `${time}, ${month} ${date}, ${year}`;
  }

  React.useEffect(() => {
    // TODO: remove the duplicated APY calculation code.
    if (market && props.address && !props.anchor) {
      getBackendData().then((backendData)=> {
        if (backendData) {
          const oracleQuries = backendData.graphql_queries.assets;
          let filter = Object.entries(oracleQuries).filter((d) => props.address === d[1].token)[0];
          let obj = {};
          let short = Number(filter[1].statistic.apr.short);
          let long = Number(filter[1].statistic.apr.long);
          let apy = Number(market.markets[0].APY);

          let min_collateral = filter[1].statistic.minCollateralRatio;
    
          const APR = (apy * (min_collateral/(min_collateral + 1))) + (long * (2/(min_collateral + 1))) + (short * (1/(min_collateral + 1)));
          const APY = Math.pow((1+APR/365),365)
    
          obj.title = filter[1].symbol;
          obj.value = filter[1].prices.price;
          obj.address = filter[1].token;
          obj.support = ['ust'];
          obj.short = short;
          obj.long = long;
          obj.chain = 'TERRA';
          obj.minCollateral = min_collateral;
          obj.APY = APY;
          setInvestData(obj);
        }
      });
    }
    else if (props.anchor) {
      const currentNetwork = network.chainID === "bombay-12" ? { id: 1, network: bombay12 } : { id: 0, network: columbus5 };
      const addressProvider = new AddressProviderFromJson(currentNetwork.network);
      const getBalance = async () => {
        const aTerra = await terraClient.wasm.contractQuery(addressProvider.aTerra(), { balance: { address: holderAddress } });
        return { aTerra };
      };
      getBalance().then((d) => {
        const { aTerra } = d;
        setInvestData({
          amount: aTerra.balance,
          address: addressProvider.aTerra(),
          network: addressProvider.data.mmMarket,
          chain: "terra",
          support: ['ust'],
          ticker: "anc",
          title: "Anchor Protocol",
          apy: market.markets[0].APY
        });
      })
    }
  }, [props.address, getBackendData, network, holderAddress, props.anchor, market, terraClient]);

  React.useEffect(() => {
    if (investData) {
      // Special case Anchor protocol: market hour is not applicable.
      if (investData.title === "Anchor Protocol") {
        setMarket(true);
      } else {
        isMarketHour(investData.address).then((marketHour) =>  {
          setMarket(marketHour);
        });
      }
    }
  },[investData]) // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <React.Fragment>
      <div className="investAsset">
        <div className="assetTop">
          <div className="flex">
            <img src={props.anchor ? anchor : assetImg} alt="delta-neutral ust" />
            <div>
              {
                props.anchor ?
                <span className="flex"><h3>Anchor Protocol</h3></span>
                : 
                <span className="flex"><h3>Delta-neutral-{props.name && props.name}</h3><p className="fontOrange">invest+</p></span>
              }
              <p>Terra</p>
            </div>
          </div>
          {!props.anchor && <p style={{color:'#B1B5C3'}}>{text.time} {props.time && getTime(props.time)}</p> }
        </div>
        <div className="assetBottom">
          <div>
            <span className="textUpper">{text.apy}</span>
            {
              props.anchor ? 
              <h3>{Number(anchorApy*100).toFixed(2)}%</h3> 
              :
              <h3>{props.initial && props.value && calcApy(props.initial, props.value)}</h3> 
            }
          </div>
          <div className="flex">
            {
              !props.anchor && !marketHour &&
              <div className="helpClass">
                <Tooltip title={text.helperText}>
                  <HelpIcon />
                </Tooltip>
              </div>
            }
            <button
              disabled={!props.anchor && !marketHour}
              className={!props.anchor && !marketHour ? 'disabled button' : 'button'}
              onClick={() => handleInvest('withdraw')}>
              {text.withdraw}
            </button>
            <button
              disabled={!props.anchor && !marketHour}
              className={!props.anchor && !marketHour ? 'disabled buttonColor' : 'buttonColor'}
              onClick={() => handleInvest('invest')}>
              {text.invest}
            </button>
          </div>
        </div>
      </div>
      { props.anchor && openInvest &&
        <Asset
          invest
          data={investData}
          img1={terra}
          img2={anchor}
          img={ust}
          token={props.address}
          close={handleClose}
        />
      }
      { props.anchor && openWithdraw &&
        <Asset
          withdrawValue={(anchorInvestment.value*exchange.exchange_rate)/1e6}
          data={investData}
          img1={terra}
          img2={anchor}
          img={ust}
          token={props.address}
          close={handleClose}
        />
      }
      { props.address && openInvest &&
        <Asset
          plus
          invest
          data={investData}
          img1={terra}
          img2={anchor}
          img={assetImg}
          token={props.address}
          close={handleClose}
        />
      }
      { props.address && openWithdraw &&
        <Asset
          plus
          currentValue={props.value}
          data={investData}
          img1={terra}
          img2={anchor}
          img={assetImg}
          token={props.address}
          close={handleClose}
          position={props.position}
        />
      }
    </React.Fragment>
  )
}