import * as React from 'react';
import { LCDClient } from '@terra-money/terra.js';
import { useWallet } from '@terra-money/wallet-provider';
import axios from 'axios';

const AppContext = React.createContext();

export function useApp() {
  return React.useContext(AppContext);
}

const AppProvider = ({ children }) => {
  const [language, setLanguage] = React.useState('english');
  const [width, setwidth] = React.useState(window.innerWidth);
  const { network } = useWallet();

  React.useEffect(() => {
    // Check local storage on page load to get last saved language
    const langCheck = localStorage.getItem('language');
    if (langCheck !== null) {
      setLanguage(langCheck);
    }
    // Set primary language to english
    else {
      setLanguage('english');
    }
  }, []);

  // Set Width
  React.useEffect(() => {
    window.addEventListener('resize', (x) => setwidth(x.target.innerWidth));
  }, [width]);

  const getBackendData = async () => {
    const res = await axios.get(
      'https://nh4146kf45.execute-api.us-west-2.amazonaws.com/prod/frontend-data',
      {
        params: {
          network: network.chainID === 'bombay-12' ? 'testnet' : 'mainnet',
        },
      }
    );
    return res.data;
  };

  const isMarkeHourCondition = (timestamp_sec) => {
    // Treat data updated within last 2 minutes as within market hour.
    return new Date() - new Date(parseInt(timestamp_sec) * 1e3) <= 2 * 60 * 1e3;
  };

  const isMarketHour = async (mAssetAddr) => {
    const mainnetAAPL = 'terra1vxtwu4ehgzz77mnfwrntyrmgl64qjs75mpwqaz';
    const testnetAAPL = 'terra16vfxm98rxlc8erj4g0sj5932dvylgmdufnugk0';
    const defaultAssetAddr =
      network.chainID === 'columbus-5' ? mainnetAAPL : testnetAAPL;
    // Default to AAPL to test market hour condition.
    if (!mAssetAddr) {
      mAssetAddr = defaultAssetAddr;
    }
    const oracleQueries = (await getBackendData()).oracle_queries;
    const lastUpdatedBase = oracleQueries[mAssetAddr].last_updated_base;
    return isMarkeHourCondition(lastUpdatedBase);
  };

  // Langauge update function
  const langSelect = (lan) => {
    // Update state of language in application and local storage
    setLanguage(lan);
    localStorage.setItem('language', lan);
  };

  // Check if has access
  const setAccess = () => {
    localStorage.setItem('hasAccess', true);
  };
  const getAccess = localStorage.getItem('hasAccess');

  // Check if accepted
  const setAccept = () => {
    // Update state of language in application and local storage
    localStorage.setItem('checkDisclaimer', true);
  };
  const getAccept = localStorage.getItem('checkDisclaimer');

  // Token gas price switch
  const gasFeeName = (name) => {
    switch (name) {
      case 'uusd':
        return 'UST';
      case 'uluna':
        return 'Luna';
      case 'ukrw':
        return 'KRT';
      case 'usdr':
        return 'SDT';
      case 'umnt':
        return 'MNT';
      case 'ueur':
        return 'EUT';
      case 'ucny':
        return 'CNT';
      case 'ujpy':
        return 'JPT';
      case 'ugbp':
        return 'GBT';
      case 'uinr':
        return 'INT';
      case 'ucad':
        return 'CAT';
      case 'uchf':
        return 'CHT';
      case 'uaud':
        return 'AUT';
      case 'usgd':
        return 'SGT';
      case 'uthb':
        return 'THT';
      case 'uhkd':
        return 'HKT';
      default:
        return;
    }
  };

//   const clientUrl =
//     network.chainID === 'columbus-5'
//       ? 'https://thrumming-wispy-snow.terra-mainnet.quiknode.pro/767455606a361ac0fdfba9d94fbc16f307f4096b/'
//       : network.lcd;
  const terraClient = new LCDClient({
    URL: network.lcd,
    chainID: network.chainID,
  });

  return (
    <AppContext.Provider
      value={{
        width,
        language,
        setAccess,
        getAccess,
        setAccept,
        getAccept,
        langSelect,
        gasFeeName,
        isMarkeHourCondition,
        isMarketHour,
        getBackendData,
        terraClient,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export { AppProvider, AppContext };
