import React from 'react'
import { useToken } from '../../context/TokenContext';
import { useDelta } from '../../context/DeltaContext';
import { useWallet } from '@terra-money/wallet-provider';
import Spinner from './Spinner';
import Success from './Success';
import { useApp } from '../../context/AppContext';

export default function Pending() {

  const { network } = useWallet();
  const { terra } = useApp();
  const { progress, setProgress, getAnchor } = useToken();
  const { refreshPositions, clearBalances } = useDelta();
  const [check, setCheck] = React.useState(false);

  React.useEffect(() => {
    let checkProgress = (tries, executor) => {
      --tries;
      return new Promise(executor)
        .catch(err => tries > 0 ? checkProgress(tries, executor) : Promise.reject(err));
    };
    if (progress.length !== 0) {

      let prog = progress[progress.length-1];
      checkProgress(10, (resolve, reject) => {
        let func = terra.tx.txInfo(prog);
        try {
          setTimeout(() => {
            resolve(func)
          }, 2000)
        } catch (e) {
          reject(func)
        }
      })
      .then(result => {
        setCheck(true);
        setTimeout(() => {
          setProgress(
            progress.filter(fil => fil !== prog)
          )
          refreshPositions();
          clearBalances();
          getAnchor();
          setCheck(false);
        }, 2500)
      })
      .catch(err => err);
    }

  }, [progress, network, setProgress]) // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div className="pendingContainer">
      <button className="buttonColor">
        {
          check ? <Success />
          : <React.Fragment>{ <Spinner /> } { progress.length } Pending...</React.Fragment>
        }
      </button>
    </div>
  )
}