import * as React from 'react';
import {
  useWallet,
  CreateTxFailed,
  Timeout,
  TxFailed,
  TxUnspecifiedError,
  useConnectedWallet,
  UserDenied,
} from '@terra-money/wallet-provider';
import axios from 'axios';
import { Coins, Coin, MsgExecuteContract } from '@terra-money/terra.js';
import { AnchorEarn, DENOMS, Account, CHAINS } from '@anchor-protocol/anchor-earn';
import { AnchorToken, columbus5, bombay12, AddressProviderFromJson } from '@anchor-protocol/anchor.js'
import { GET_BOMBAY_INFO, GET_COLUMBUS_INFO, GET_COLUMBUS_COMMISSION, DEFAULT_INFO } from '../data/export'
import { useApp } from './AppContext';

const TokenContext = React.createContext();

export function useToken() {
  return React.useContext(TokenContext);
}

const TokenProvider = ({children}) => {

  const { terraClient } = useApp();
  const {
    status,
    network,
    wallets,
  } = useWallet();
  const connectedWallet = useConnectedWallet();
  const [coins, setCoins] = React.useState();
  const [gasFees, setGasFees] = React.useState();
  const [selectGas, setSelectGas] = React.useState({name:'uusd', price:'0.2'});
  const [ancInvFee, setAncInv] = React.useState({ amount: 0 });
  const [deltaInvFee, setDeltaInv] = React.useState({ amount: 0 });
  const [anchorApy, setAnchorApy] = React.useState();
	const [holderAddress, setHolderAddr] = React.useState();
  const [balance, setBalance] = React.useState();
  const [anchorValue, setAnchorValue] = React.useState();
  const [anchorMMarket, setAnchorMarket] = React.useState();
  const [tokenList, setTokenList] = React.useState(null);
  const [result, setResult] = React.useState();
  const [error, setError] = React.useState();
  const [errMsg, setErrMsg] = React.useState();
  const [progress, setProgress] = React.useState([]);
  const [loading, setLoading] = React.useState();
  const [success, setSuccess] = React.useState();
  const [deposit, setDeposit] = React.useState();
  const [query, setQuery] = React.useState(DEFAULT_INFO);
  const [queryAPR, setQueryAPR] = React.useState(DEFAULT_INFO);
  const [market, setMarketInfo] = React.useState();
  const [exchange, setExchange] = React.useState();
  const terra_manager_address = network.chainID === "bombay-12" ? 'terra1pzmq3sacc2z3pk8el3rk0q584qtuuhnv4fwp8n' : 'terra1ajkmy2c0g84seh66apv9x6xt6kd3ag80jmcvtz';

  React.useEffect(() => {
    if (network) {
      let networkReq = network.chainID === "bombay-12" ? "https://bombay-fcd.terra.dev/v1/txs/gas_prices" : "https://fcd.terra.dev/v1/txs/gas_prices";
      axios.get(networkReq)
        .then((res) => {
          const resFees = Object.entries(res.data).filter(f => {
            return ['uusd','uluna','ukrw','usdr','umnt','ueur','ucny','ujpy','ugbp','uinr','ucad','uchf','uaud','usgd','uthb','uhkd'].indexOf(f[0]) !== -1
          });
          let filteredFees = resFees.map(d => {
            return { name: d[0], price: d[1] }
          });
          setGasFees(filteredFees);
        })
    }
  },[network])

  const getAnchor = () => {
    const account = new Account(CHAINS.TERRA); 

    const currentNetwork = network.chainID === "bombay-12" ? { id: 1, network: bombay12 } : { id: 0, network: columbus5 };

    const anchorEarn = new AnchorEarn({
      chain: CHAINS.TERRA,
      network: currentNetwork.id,
      address: account.accAddress,
    });
    const addressProvider = new AddressProviderFromJson(currentNetwork.network);
    setAnchorMarket(addressProvider.data.mmMarket);
    
    const anchorRequest = new AnchorToken(terraClient, addressProvider);

    async function getInfo() {
      const marketInfo = await anchorEarn.market({
        currencies: [
          DENOMS.UST
        ],
      });
      const anchorVal = await anchorRequest.getANCPrice();

      const collaterals = await fetch(`https://api${network.chainID === "bombay-12" ? '-testnet' : ''}.anchorprotocol.com/api/v1/collaterals`)
      .then(response => response.json())
      .then(data => data);

      const deposits = await fetch(`https://api${network.chainID === "bombay-12" ? '-testnet' : ''}.anchorprotocol.com/api/v1/deposit`)
      .then(response => response.json())
      .then(data => data)

      return { marketInfo, anchorVal, anchorEarn, collaterals, deposits };
    };
    getInfo().then((data) => {
      const { marketInfo, collaterals, deposits, anchorVal, anchorEarn } = data;
      setAnchorValue(anchorVal);
      setMarketInfo(marketInfo);
      setAnchorApy(data.marketInfo.markets[0].APY);
      const tokenData = {
        investPlus: [
        ],
        invest: [
          {
            title: 'Anchor Protocol',
            action: true,
            tvl: Number((collaterals.total_value/1000000).toFixed(2))+Number((deposits.total_ust_deposits/1000000).toFixed(2)),
            apy: data.marketInfo.markets[0].APY,
            ticker: 'anc',
            chain: 'terra',
            support: ['ust'],
            value: anchorValue,
            address: anchorEarn.earn._addressProvider.data.mmMarket
          },
        ]
      };
      setTokenList(tokenData)
    });
  };

  const setUserCoins = () => {
    const coins = async () => {
      let bal = await terraClient.bank.balance(holderAddress);
      return bal;
    }
    coins().then((bal) => {
      let newCoin = new Coins(bal[0]).toData();
      setCoins(newCoin)
    })
  };

  React.useEffect(() => {
    if (holderAddress && network && status !== 'INITIALIZING') {
      setUserCoins();
    }
  },[holderAddress, network, status])

  React.useEffect(() => {
    if (network && status !== 'INITIALIZING') {
      getAnchor();
    }
  }, [anchorValue, balance, wallets, network, status]); // eslint-disable-line react-hooks/exhaustive-deps


  React.useEffect(() => {
    if (network && status !== 'INITIALIZING') {
      const queryInfo = network.chainID === 'bombay-12' ? GET_BOMBAY_INFO : GET_COLUMBUS_INFO;
      setQuery(queryInfo);
      setQueryAPR(GET_COLUMBUS_COMMISSION);
    }
  }, [network, wallets, status])


  React.useEffect(() => {
    if (network && holderAddress && status !== 'INITIALIZING' && status !== 'WALLET_NOT_CONNECTED') {

      const currentNetwork = network.chainID === "bombay-12" ? { id: 1, network: bombay12 } : { id: 0, network: columbus5 };
      const anchorEarn = new AnchorEarn({
        chain: CHAINS.TERRA,
        network: currentNetwork.id,
        address: holderAddress,
      });
      const getUserToken = () => {
        async function getInfo() {
          const balanceInfo = await anchorEarn.balance({
            currencies: [
              DENOMS.UST
            ],
          });
          return { balanceInfo, anchorEarn };
        };
        getInfo().then((data) => {
          const { total_account_balance_in_ust, total_deposit_balance_in_ust } = data.balanceInfo;
          setBalance(total_account_balance_in_ust);
          setDeposit(total_deposit_balance_in_ust)
        })
      };
      getUserToken();
    }
  }, [network, holderAddress, tokenList, status]);

  const getFees = () => {
    if (gasFees && holderAddress && balance && terra_manager_address && anchorMMarket) {
      async function getGasFee(msgs) {
        const accountInfo = await terraClient.auth.accountInfo(holderAddress);
        let denoms = selectGas.name;
        let fees = selectGas.price;
        const feeDenoms = [denoms];
        const gasPrices = {[denoms]:fees}
        const txOptions: CreateTxOptions = { msgs, gasPrices, feeDenoms, };
        // Test raw estimate fee function with specified gas
        const rawFee = await terraClient.tx.estimateFee(
          [ { sequenceNumber: accountInfo.getSequenceNumber(), publicKey: accountInfo.getPublicKey(), }, ],
          txOptions
        );
        return rawFee;
      };

      let anchorMsgs = [
        new MsgExecuteContract( holderAddress, anchorMMarket, { "deposit_stable": {} }, [new Coin("uusd", (balance*1000000).toFixed(0).toString())] )
      ];

      let encAddress = network.chainID === "bombay-12" ? "terra1ys4dwwzaenjg2gy02mslmc96f267xvpsjat7gx" : "terra1dk3g53js3034x4v5c3vavhj2738une880yu6kx";
      let encode_data = { "target_min_collateral_ratio": "2.5", "target_max_collateral_ratio": "2.9", "mirror_asset_cw20_addr": encAddress };
      let encoding = btoa(JSON.stringify(encode_data));

      const investMsg = [
        new MsgExecuteContract(
          holderAddress, terra_manager_address,
          {
            "create_position": {
              "data": encoding,
              "assets": [ { "info": { "native_token": { "denom": "uusd" } }, "amount": (balance*1000000).toFixed(0).toString() } ],
              "strategy": { "chain_id": 3, "strategy_id": "0" }
            }
          },
          [new Coin("uusd", (balance*1000000).toFixed(0).toString())]
        )
      ];
      balance !== '0' && getGasFee(anchorMsgs).then((d) => setAncInv(d.toData().amount[0]));
      balance !== '0' && Number(balance) >= 500 && getGasFee(investMsg).then((d) => setDeltaInv(d.toData().amount[0]));
    }
  };

  React.useEffect(() => {
    getFees();
  },[gasFees, selectGas, balance, holderAddress, terra_manager_address, anchorMMarket]) // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (market) {
      const currentNetwork = network.chainID === "bombay-12" ? { id: 1, network: bombay12 } : { id: 0, network: columbus5 };
      const addressProvider = new AddressProviderFromJson(currentNetwork.network);
      let marketContractAddress = addressProvider.market();

      const getExchange = () => {
        async function asyncExchange() {
          const exchange = await terraClient.wasm.contractQuery(marketContractAddress, { epoch_state: { } });
          return exchange;
        }
        asyncExchange().then((data) => {
          setExchange(data);
        })
      };
      getExchange();
    }
  },[market, network, terraClient])

  React.useEffect(() => {
    if (result) {
      const tx = result.result.txhash;
      setProgress(p => [...p, tx])
      setLoading(false);
      setSuccess(true);
    }
  },[result])

  React.useEffect(() => {
    if (error) {
      setErrMsg(error);
      setTimeout(() => {
        setErrMsg()
      }, 10000);
    }
  },[error])

  const executeInvest = (msgs, gasFee, type) => {
    if (!connectedWallet) {
      return;
    }

    setResult(null);
    setError(null);

    connectedWallet
      .post({
        fee: gasFee,
        msgs: msgs,
      })
      .then((res) => {
        setResult(res);
      })
      .catch((err) => {
        setLoading(false);
        if (err instanceof UserDenied) {
          setError('Error: User has cancelled the transaction, please try again.');
        } else if (err instanceof CreateTxFailed) {
          setError('Error: Create Tx failed, please try again.');
        } else if (err instanceof TxFailed) {
          setError('Error: Tx failed, please try again.' + String(err.message));
        } else if (err instanceof Timeout) {
          setError('Error: Network has timed out, please try again.');
        } else if (err instanceof TxUnspecifiedError) {
          setError('Error: Unspecified error, please try again.');
        } else {
          setError(
            'Unknown Error: ' +
              (err instanceof Error ? err.message : String(err)),
          );
        }
      });

  };

	const getToken = (t) => {
		setHolderAddr(t);
	}

  const deleteToken = (t) => {
    setBalance();
  }

  const parseNum = (num) => {
    return Number(Number(num).toFixed(2)).toLocaleString();
  }

  React.useEffect(() => {
    status === 'WALLET_CONNECTED' && getToken(wallets[0].terraAddress);
  }, [status, wallets])

  return (
    <TokenContext.Provider value={{
      coins,
      gasFees,
      anchorApy,
      query,
      queryAPR,
      getFees,
      market,
      loading,
      success,
      errMsg,
      setErrMsg,
      setSuccess,
      setLoading,
      exchange,
      selectGas,
      setSelectGas,
      progress,
      parseNum,
      ancInvFee,
      deltaInvFee,
      setProgress,
    	holderAddress,
      getAnchor,
    	getToken,
      balance,
      deposit,
      tokenList,
      deleteToken,
      anchorMMarket,
      executeInvest,
      setUserCoins,
      terra_manager_address
    }}>
      {children}
    </TokenContext.Provider>
  )
}

export { TokenProvider,  TokenContext };