import React from 'react'
import { useToken } from '../../context/TokenContext';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';

// Material 
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Fade from '@mui/material/Fade';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

export default function GasSelector() {

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const { language, gasFeeName } = useApp();
  const { gasFees, selectGas, setUserCoins, setSelectGas } = useToken();
  const text = textContext[language].assets;

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleSelect = (val) => {
    setUserCoins();
    setSelectGas(val)
    setAnchorEl(null);
  };

  React.useEffect(() => {
    setSelectGas({name: 'uusd', price:'0.2'})
    setUserCoins();
  },[setSelectGas]);

  return (
    <div className="gasSelector wrapper">
      <span>
        {text.fees}:
        <div
          id="gas-select"
          aria-controls={open ? 'fade-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
          onClick={handleClick}
        >
          {gasFeeName(selectGas.name)} <ExpandMoreIcon />
        </div>
      </span>
      <Menu
        id="gas-menu"
        MenuListProps={{
          'aria-labelledby': 'fade-button',
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        TransitionComponent={Fade}
      >
        {
          gasFees.map((arr,i) => <MenuItem key={i} onClick={() => handleSelect(arr)} value={arr.price}>{gasFeeName(arr.name)}</MenuItem> )
        }
      </Menu>
    </div>
  )
}