import { useState, useEffect } from 'react';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useToken } from '../../context/TokenContext';
import { useWallet } from '@terra-money/wallet-provider';
import * as AWS from 'aws-sdk';
import PropTypes from 'prop-types';

// Comps
import Search from './Search';
import Invest from './Invest';

export default function InvestPanel(props) {
  const { language, getBackendData, isMarkeHourCondition } = useApp();
  const { network } = useWallet();
  const { tokenList, market } = useToken();
  const text = textContext[language].investment.invest;
  const [mAssetsList, setMAssetsList] = useState([]);
  const [mAssetOracles, setMAssetOracles] = useState({});
  const [search, setSearch] = useState('');
  const [investData, setInvestData] = useState([]);
  const [investPlus, setInvestPlus] = useState([]);

  // Query mAsset TVL from DynamoDB and persist it into state.
  // TODO: Update tvl table to only contain latest data, then scan the
  //       table for much better performance.
  useEffect(() => {
    const documentClient = new AWS.DynamoDB.DocumentClient({
      region: 'us-west-2',
    });  
    const getMAssets = async () => {
      const backendData = await getBackendData();
      const assets = backendData.graphql_queries.assets;
      setMAssetOracles(backendData.oracle_queries);
      const tableName =
        network.chainID === 'bombay-12' ? 'strategy_tvl_dev' : 'strategy_tvl';
      let totalTvl = 0;
      let totalCount = 0;

      assets.forEach(async (item) => {
        const response = await documentClient
          .query({
            TableName: tableName,
            KeyConditionExpression: 'strategy_id=:strategy_id',
            ExpressionAttributeValues: {
              ':strategy_id': item.token,
            },
            ScanIndexForward: 'False',
            Limit: 1,
          })
          .promise();
        const tvl = response.Items[0]?.tvl_uusd;
        if (tvl) {
          item.tvl = Number(response.Items[0]?.tvl_uusd) / 1e6;
          totalTvl += item.tvl;
        }
        totalCount++;
        if (totalCount === assets.length) {
          props.handleTotalTvlCalculated(
            Number(Number(totalTvl).toFixed(2)).toLocaleString()
          );
          setMAssetsList(assets);
        }
      });
    };
    getMAssets();
  }, [getBackendData, network, props]);

  useEffect(() => {
    if (tokenList !== null) {
      setInvestData(tokenList.invest);
    }
  }, [tokenList, search]);

  // Use query data from above to calculate & populate for each row.
  useEffect(() => {
    const investPlusData = [];
    if (mAssetsList && market && investPlus.length === 0) {
      Object.entries(mAssetsList).forEach((d, i) => {
        const assetInfo = d[1];
        let obj = {};
        obj.address = assetInfo.token;
        obj.title = assetInfo.symbol;
        obj.name = assetInfo.name;
        obj.tvl = assetInfo.tvl || 0;
        const oracleInfo = mAssetOracles[assetInfo.token];
        // Some mAsset may not have oracle information due to error from
        // our backend API. If so, skip them for now.
        if (oracleInfo) {
          obj.action = isMarkeHourCondition(oracleInfo.last_updated_base);
          let short = Number(assetInfo.statistic.apr.short);
          let long = Number(assetInfo.statistic.apr.long);
          let anchorAPY = Number(market.markets[0].APY);
          let min_collateral = assetInfo.statistic.minCollateralRatio;
          let defaultCol = 1.8 * Number(min_collateral);
          let APR =
            anchorAPY * (defaultCol / (defaultCol + 1)) +
            long * (2 / (defaultCol + 1)) +
            short * (1 / (defaultCol + 1));
          let APY = Math.pow(1 + APR / 365, 365) - 1;

          obj.value = assetInfo.prices.price;
          obj.support = ['ust'];
          obj.short = short;
          obj.long = long;
          obj.chain = 'TERRA';
          obj.minCollateral = min_collateral;
          obj.APY = APY;
          investPlusData.push(obj);
        }
      });
      setInvestPlus(investPlusData);
    }
  }, [market, mAssetsList]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleSearch = (fil) => {
    setSearch(fil);
  };

  return (
    <div>
      <Search handleSearch={handleSearch} />
      <Invest
        plus
        data={investPlus}
        search={search}
        filters={text.filterPlus}
        title={text.title.investPlus}
      />
      <Invest
        data={investData}
        search={search}
        filters={text.filter}
        title={text.title.invest}
      />
    </div>
  );
}

InvestPanel.propTypes = {
  handleTotalTvlCalculated: PropTypes.func.isRequired,
};
