import React from 'react';
import './App.css';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import GlobalStyle from './styles/GlobalStyle';

// Context wrapper for text and width
import { AppProvider } from './context/AppContext';

// Context wrapper for theme
import { ThemeProvider } from './context/ThemeContext';
import { theme } from './styles/theme';

// Context wrapper for tokens
import { TokenProvider } from './context/TokenContext';
import { DeltaProvider } from './context/DeltaContext';

// Comps
import Navbar from './components/layout/Navbar';
import Banner from './components/layout/Banner';
import Disclaimer from './components/layout/Disclaimer';
import LaunchModal from './components/layout/LaunchModal';

// Pages
import Investment from './pages/Investment';
import Portfolio from './pages/Portfolio';
import News from './pages/News';
import Settings from './pages/Settings';
import Privacy from './pages/Privacy';
import Terms from './pages/Terms';
import Credits from './pages/Credits';

// GraphQL
import { ApolloClient } from 'apollo-client';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { HttpLink } from 'apollo-link-http';
import { ApolloProvider } from '@apollo/react-hooks';

// Terra
import { useWallet } from '@terra-money/wallet-provider';

function App() {
  const { network } = useWallet();
  const [net, setNet] = React.useState(network.chainID);

  React.useEffect(() => {
    net !== network.chainID && setNet(network.chainID);
  }, [net, network.chainID]);

  const cache = new InMemoryCache();
  const link = new HttpLink({
    uri:
      net === 'bombay-12'
        ? 'https://bombay-graph.mirror.finance/graphql'
        : 'https://graph.mirror.finance/graphql',
  });

  const client = new ApolloClient({
    cache,
    link,
  });

  return (
    <BrowserRouter>
      <AppProvider>
        <LaunchModal />
        <TokenProvider>
          <DeltaProvider>
            <ThemeProvider theme={theme}>
              <ApolloProvider client={client}>
                <GlobalStyle />
                <Navbar />
                <Banner />
                <Disclaimer />
                <Routes>
                  <Route path="/" element={<Investment />} />
                  <Route path="/invest/*" element={<Investment />} />
                  <Route path="/portfolio" element={<Portfolio />} />
                  <Route path="/news" element={<News />} />
                  <Route path="/settings" element={<Settings />} />
                  <Route path="/privacy" element={<Privacy />} />
                  <Route path="/terms" element={<Terms />} />
                  <Route path="/credits" element={<Credits />} />
                </Routes>
              </ApolloProvider>
            </ThemeProvider>
          </DeltaProvider>
        </TokenProvider>
      </AppProvider>
    </BrowserRouter>
  );
}

export default App;
