import React from 'react'
import { useWallet } from '@terra-money/wallet-provider';
import { useDelta } from '../context/DeltaContext';
import { useToken } from '../context/TokenContext';
import { useApp } from '../context/AppContext';
import Drawer from '@mui/material/Drawer';
import textContext from '../textContext';
import ArrowDown from '@mui/icons-material/KeyboardArrowDown';

// Comps
import Total from '../components/Portfolio/Total';
import Trades from '../components/Portfolio/Trades';
import UserAccount from '../components/Portfolio/UserAccount';
import UnauthAccount from '../components/Portfolio/UnauthAccount';
import SelectedInvest from '../components/Portfolio/SelectedInvest';

// Material UI
import Grid from '@mui/material/Grid';

const Dropdown = ({children, title, position}) => {

  const { width, language } = useApp();
  const text = textContext[language].portfolio;
  const [open, setOpen] = React.useState(false);
  // Toggle drawer
  const toggleDrawer = (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setOpen(!open);
  };

  React.useEffect(() => {
    width > 1199 && setOpen(false);
  },[width])

  return (
    <React.Fragment>
      {
        width < 1200 ?
        <div className="portfolioDrawer">
          <div className="header">
            <h4>{text.title}</h4>
            <div onClick={toggleDrawer}>
              <p>{title}</p>
              <ArrowDown style={open ? {transform:'rotate(180deg)'} : {}} />
            </div>
          </div>
          <Drawer open={open} anchor='top' className="portDrawer">
            <div onClick={toggleDrawer}>{children}</div>
          </Drawer>
        </div>
        :
        <span>{children}</span>
      }
    </React.Fragment>
  )
};

export default function Portfolio() {

  const { width, language } = useApp();
  const text = textContext[language].portfolio;
  const { status } = useWallet();
  const { deltaSum } = useDelta();
  const [position, setPosition] = React.useState();
  const [title, setTitle] = React.useState(text.total);
  const { progress } = useToken();

  const handleSelect = (select) => {
    setTitle(select.title);
    setPosition(select.position);
  }

  React.useEffect(() => {
    setPosition();
  },[progress])

  React.useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  return (
    <div id="portfolio" className={width < 1200 ? '' : 'container'}>
    <p className="header">Portfolio</p>
      <Grid container spacing={2}>
        <Grid className="rightTab" item xs={12} lg={4} xl={3}>
          <Dropdown title={title} position={position}>
            <Total handleSelect={handleSelect} position={position} total={deltaSum} />
            { status && status === "WALLET_CONNECTED" && <Trades selected={position} handleSelect={handleSelect} /> }
          </Dropdown>
        </Grid>
        <Grid item xs={12} lg={8} xl={9}>
          {
            status && status === "WALLET_CONNECTED" ?
            ( position ? <SelectedInvest position={position} /> : <UserAccount handleSelect={handleSelect} total={deltaSum} /> )
            :
            <UnauthAccount />
          }
        </Grid>
      </Grid>
    </div>
  )
}