import React from 'react'
import { anchor } from '../../assets/images/icons';
import { useToken } from '../../context/TokenContext';
import { useApp } from '../../context/AppContext';
import LinearProgress from '@mui/material/LinearProgress';
import styled from 'styled-components';
import { useTheme } from '../../context/ThemeContext';

const BorderLinearProgress = styled(LinearProgress)`
  width: 100%;
  height: 28px;
  opacity: .1;
  border-radius: 8px;
  position: relative;
  top: -3px;
`;

export default function Trade(props) {

  const { width } = useApp();
  const { exchange, parseNum } = useToken();
  const { position } = props;

  const { palette } = useTheme();
  const assetImg = position.name && (
    palette.name === 'light'
    ? `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons/${position.name.substring(1)}.png`
    : `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons_dark/${position.name.substring(1)}.png`
  );

  const handleSelect = () => {
    props.handleSelect({ position: props.anchor ? 'anc' : position.position, title: props.anchor ? 'Anchor' : `delta-neutral-${position.name}` })
  };

  return (
    <React.Fragment>
    {
      props.anchor &&
      <div onClick={handleSelect} className={`wrapper ${props.selected && 'selected'}`}>
        <div className="trades">
          { width > 1199 && <img src={anchor} alt="anchor" width="40" /> }
          <div className="content">
            <h4>Anchor Protocol</h4>
            { width > 1199 && <span>Terra</span> }
            <h4>${props.anchor && exchange && parseNum((Number(props.anchor)/1000000)*Number(exchange.exchange_rate))}</h4>
          </div>
        </div>
        {
          props.progress &&
          <React.Fragment>
            <hr className="progressHr" />
            <div className="progress">
              in progress
            </div>
          </React.Fragment>
        }
      </div>
    }
    {
      position && position.value && position.address &&
      <div onClick={handleSelect} className={`wrapper ${props.selected && 'selected'}`}>
        <div className="trades">
          {
            !position.name ?
            <BorderLinearProgress /> : 
            <React.Fragment>
              { width > 1199 && <img src={assetImg} alt="delta-neutral ust" width="40" /> }
              <div className="content">
                <h4>Delta-neutral-{position.name}</h4>
                { width > 1199 && <span>Terra</span> }
                <h4>${parseNum(position.value)}</h4>
              </div>
            </React.Fragment>
          }
        </div>
        {
          position.progress &&
          <React.Fragment>
            <hr className="progressHr" />
            <div className="progress">
              in progress
            </div>
          </React.Fragment>
        }
      </div>
    }
    </React.Fragment>
  )
}