import React from 'react'
import styled from 'styled-components';
import { useApp } from '../context/AppContext';
import { useToken } from '../context/TokenContext';
import { useTheme } from '../context/ThemeContext';
import { useDelta } from '../context/DeltaContext';
import textContext from '../textContext';
import { ust } from '../assets/images/icons';
import { Coin, MsgExecuteContract } from '@terra-money/terra.js';
import NumberFormat from 'react-number-format';

// Comps
import Spinner from '../components/layout/Spinner';
import Success from '../components/layout/Success';
import GasSelector from '../components/Asset/GasSelector';
import AssetSearch from '../components/Asset/AssetSearch';
import ErrorMessage from '../components/Asset/ErrorMessage';

// Material
import Slider from '@mui/material/Slider';
import InputBase from '@mui/material/InputBase';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Popover from '@mui/material/Popover';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import CloseIcon from '@mui/icons-material/Close';
// import ArrowDown from '@mui/icons-material/KeyboardArrowDown';
// import ChevronRightIcon from '@mui/icons-material/ChevronRight';

const Close = styled('span')`
  margin: 0rem!important;
  position: absolute!important;
  right: 15px!important;
  top: 20px!important;
  cursor: pointer;
  @media (max-width: 1199px) {
    right: initial!important;
    left: 20px!important;
    top: 7.5px!important;
  }
`;

export default function Asset(props) {
  React.useEffect(() => {
    window.scrollTo(0, 0)
    }, [])

  const { language, width, gasFeeName, terraClient } = useApp();
  const {
    balance,
    getFees,
    loading,
    success,
    holderAddress,
    anchorApy,
    errMsg,
    ancInvFee,
    deltaInvFee,
    setErrMsg,
    setLoading,
    setSuccess,
    executeInvest,
    anchorMMarket,
    exchange,
    selectGas,
    parseNum,
    coins,
    terra_manager_address
  } = useToken();
  const { posManager } = useDelta();
  const text = textContext[language].assets;
  const {
    investText,
    withdrawText,
    strategyText,
    tokenText,
    feeText,
    gasText,
    strategyFeeText,
    targetText,
    apyText,
    apyTitle,
    max,
    minInvest,
    maxDelta,
    maxBalance,
    maxWithdraw,
    reminder1,
    reminder2,
    reminder3,
    reminder4,
  } = text;
  const {
    position, close, currentValue, invest, withdrawValue, plus, img1, img2, data: {
      address,
      title,
      chain,
      minCollateral,
      long,
      short,
    },
  } = props;
  const { palette } = useTheme();

  const defaultCol = (1.8*Number(minCollateral)*100).toFixed(0);

  const [amount, setAmount] = React.useState(currentValue ? currentValue : '');
  const [selectToken, setToken] = React.useState('ust');
  const [collateral, setCollateral] = React.useState(Number(defaultCol));

  const checkGas = coins.findIndex(fil => fil.denom === selectGas.name);
  const getGas = coins.filter(fil => fil.denom === selectGas.name)[0];

  const deltaFeeInit = deltaInvFee ? (Number(deltaInvFee.amount)/1000000).toFixed(3) : 0;
  const anchorFeeInit = ancInvFee ? (Number(ancInvFee.amount)/1000000).toFixed(3) : 0;

  const maxInvest = selectGas.name === 'uusd' ? Number(balance)-Number(anchorFeeInit) : Number(balance);
  const maxDeltaInvest = selectGas.name === 'uusd' ? Number(balance)-Number(deltaFeeInit) : Number(balance);

  const increaseRatio = (((collateral/100)/minCollateral)-1)*100;
  
  let color = increaseRatio < 50 ? {color:'#FF4242'} : increaseRatio < 80 ? {color:'#F48E00'} : {color:'#00CA9A'}

  const assetImg = title && palette.name === 'light'
    ? `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons/${title.substring(1)}.png`
    : `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons_dark/${title.substring(1)}.png`;

  const apyCal = (apy) => {
    let col = collateral/100;
    let ancApy = Number(anchorApy) * (col/(col + 1));
    let longApr = long * (2/(col + 1));
    let shortApr = short * (1/(col + 1));
    let apr = (ancApy+longApr+shortApr);
    let APY = Math.pow((1+apr/365),365)-1;
    return APY*100
  };

  const closeModal = () => {
    setSuccess(false);
    setLoading(false);
    setErrMsg();
    document.body.style.overflow = 'auto';
    close();
  };

  const handleCollateral = (event, newValue) => {
    setCollateral(newValue);
  };

  const [anchorEl, setAnchorEl] = React.useState(null);

  // const handleSelect = (event) => {
  //   setAnchorEl(event.currentTarget);
  // };

  const countDecimals = function (value) {
    if (Math.floor(value.valueOf()) === value.valueOf()) return 0;

    var str = value.toString();
    if (str.indexOf(".") !== -1 && str.indexOf("-") !== -1) {
      return str.split("-")[1] || 0;
    } else if (str.indexOf(".") !== -1) {
      return str.split(".")[1].length || 0;
    }
  }

  const handleNumber = (val) => {
    let parsed = val === '' ? 0 : parseFloat(val.replace(/,/g, ''))
    if (parsed >= 0 && countDecimals(parsed) < 7) {
      setAmount(parsed)
    }
  }

  const handleToken = (val) => {
    setToken(val);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const open = Boolean(anchorEl);

  const handleInvest = () => {
    setLoading(true);
    let type = plus ? 'delta' : 'anchor'

    let encoding;
    if (plus) {
      let encode_data = plus && {
        "target_min_collateral_ratio": ((collateral/100)-.2).toFixed(2).toString(),
        "target_max_collateral_ratio": ((collateral/100)+.2).toFixed(2).toString(),
        "mirror_asset_cw20_addr": address
      };
      encoding = btoa(JSON.stringify(encode_data));
    }

    async function getGasFee(msgs) { 

      let denoms = selectGas.name;
      let fees = selectGas.price;
      const accountInfo = await terraClient.auth.accountInfo(holderAddress);
      const feeDenoms = [denoms];
      const gasPrices = {[denoms]:fees}

      const txOptions = {
        msgs,
        gasPrices,
        feeDenoms,
      };

      // Test raw estimate fee function with specified gas
      const rawFee = await terraClient.tx.estimateFee(
        [
          {
            sequenceNumber: accountInfo.getSequenceNumber(),
            publicKey: accountInfo.getPublicKey(),
          },
        ],
        txOptions
      );
      return rawFee;
    };

    let anchorMsgs = [
      new MsgExecuteContract(
        holderAddress, anchorMMarket,
        {
          "deposit_stable": {}
        },
        [new Coin("uusd", (amount*1000000).toFixed(0))]
      )
    ];


    let ancWithdrawAmount = withdrawValue && (Number(amount)/Number(exchange.exchange_rate));

    let anchorWithdraw = [
      new MsgExecuteContract(
        holderAddress, address,
        {
          "send": {
            "amount": (ancWithdrawAmount*1000000).toFixed(0),
            "contract": props.data.network,
            "msg": "eyJyZWRlZW1fc3RhYmxlIjp7fX0="
          }
        }
      )
    ];

    if (!plus) {

      let anchorTypeMsg = invest ? anchorMsgs : anchorWithdraw;
      getGasFee(anchorTypeMsg).then((data) => {
        executeInvest(anchorTypeMsg, data, type);
      })
      .catch((err) => {
        setLoading(false)
        err.response.status === 400
        ? setErrMsg('Error: Failed to execute message, please try again.')
        : setErrMsg('Error: Unspecified error, please try again.')
      });

    } else {

      const investMsg = [
        new MsgExecuteContract(
          holderAddress, terra_manager_address,
          {
            "create_position": {
              "data": encoding,
              "assets": [
                {
                  "info": {
                    "native_token": {
                      "denom": "uusd"
                    }
                  },
                  "amount": (amount*1000000).toString()
                }
              ],
              "strategy": {
                "chain_id": 3,
                "strategy_id": "0"
              }
            }
          },
          [new Coin("uusd", (amount*1000000).toString())]
        )
      ];

      const withdrawMsg = [
        new MsgExecuteContract(
          holderAddress, posManager,
          {
            "migrate_position_contracts": {
              "positions": [
                {
                  "chain_id": 3,
                  "position_id": position && position.toString()
                }
              ],
              "position_contracts": []
            }
          },
          []
        ),
        new MsgExecuteContract(
          holderAddress, terra_manager_address,
          {
            "execute_strategy": {
              "action": {
                "close_position": {
                  "recipient": {
                    "terra_chain": {
                      "recipient": holderAddress
                    }
                  }
                }
              },
              "assets": [],
              "position_id": position && position.toString()
            }
          },
          []
        ),
      ];

      const memo = invest ? "Open delta-neutral position" : "Close delta-neutral position";
      const deltaMsgs = invest ? investMsg : withdrawMsg;

      getGasFee(deltaMsgs).then((data) => {
        executeInvest(deltaMsgs, data, type, memo);
      })
      .catch((err) => {
        setLoading(false)
        err.response.status === 400
        ? setErrMsg('Error: Failed to execute message, please try again.')
        : setErrMsg('Error: Unspecified error, please try again.')
      });
    }
  };

  const handleModal = (e) => {
    e.stopPropagation();
    if (e.target.id === 'assetModal') {
      document.body.style.overflow = 'auto';
      setLoading(false);
      setAmount(0);
      setErrMsg();
      close();
    };
  };

  React.useEffect(() => {
    document.body.style.overflow = 'hidden';
  },[])

  React.useEffect(() => {
    success && setTimeout(() => {
      setSuccess(false);
      setAmount(0);
      setErrMsg();
      document.body.style.overflow = 'auto';
      getFees();
      close();
    }, 1500)
  }, [success, getFees, close, setSuccess, setErrMsg])

  return (
    <div id="assetModal" onClick={(event) => handleModal(event)}>
      <div>
        <Close onClick={closeModal}>{ width > 1199 ? <CloseIcon /> : <ChevronLeftIcon />  }</Close>
        <h4 className="title">{invest ? investText : withdrawText}</h4>
        <hr />
        { errMsg && <ErrorMessage /> }
        <div className="assetContainer">
          <div className="strategy">
            <span className="textUpper textSecondary">{strategyText}</span>
            <div className="assetCont">
              <div className="imgContainer">
                {
                  plus ? <img src={assetImg} height="30" alt={title} /> :
                  <>
                    <img src={img1} height="30" alt={title} />
                    <img src={img2} height="30" alt={title} />
                  </>
                }
              </div>
              <div>
                <div><p>{plus && 'Delta-neutral-'}{title}</p>{plus && <p className="fontOrange">invest+</p>}</div>
                <span className="chainClass">{chain}</span>
              </div>
            </div>
          </div>
          <div className={
              (amount >= 500 && amount <= (invest && plus ? maxDeltaInvest : invest ? maxInvest : withdrawValue) && amount <= 100000 && plus)
              || (!plus && amount <= (invest && plus ? maxDeltaInvest : invest ? maxInvest : withdrawValue))
              || (amount === '' || amount === 0)
              || (plus && currentValue)
              ? 'token'
              : 'token errorColor'
            }
          >
            <div aria-describedby="token-popover" className="selectToken">
              <div className="flex">
                <p className="textUpper">{tokenText}</p>
                {/*{ width < 1200 ? <ChevronRightIcon /> : <ArrowDown sx={{textAlign:'right'}} /> }*/}
              </div>
              <div className="flex" style={{justifyContent:'flex-start',alignItems:'center'}}>
                {selectToken && <img src={ust} alt="ust" width="30" />}
                <h4 style={{padding:'1px .5rem'}}>{selectToken}</h4>
              </div>
            </div>
            <Popover
              id="token-popover"
              open={open}
              anchorEl={anchorEl}
              onClose={handleClose}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
            >
              <div>
                <div className="flex">
                  <p>Select Token</p>
                  <div onClick={handleClose}>&#x2715;</div>
                </div>
                <AssetSearch plus={plus} close={handleClose} setToken={handleToken} />
              </div>
            </Popover>
            <div className="selectValue">
              <NumberFormat
                value={amount === 0 ? '' : (amount === '' ? amount : amount)}
                onChange={(event) => handleNumber(event.target.value)}
                customInput={InputBase}
                type="text"
                decimalScale={6}
                placeholder={'0.00'}
                thousandSeparator={true}
                disabled={currentValue ? true : false}
              />
              {
                (invest || !plus) &&
                <span className="setMax">
                  <button disabled={!invest && !withdrawValue} onClick={() => setAmount(withdrawValue ? withdrawValue : plus ? maxDeltaInvest : maxInvest)} className="textUpper">{max}</button>
                </span>
              }
              {
                (invest || !plus) && 
                <div className="flex">
                  {
                    amount !== 0 && amount !== '' && amount > 100000 && plus
                    ? <p className="alertText">{maxDelta}</p>
                    : !plus && amount > withdrawValue
                    ? <p className="alertText">{maxWithdraw}: {withdrawValue.toFixed(6)}</p>
                    : !withdrawValue && amount > (plus ? maxDeltaInvest : maxInvest) && amount !== '' && amount !== 0
                    ? <p className="alertText">{maxBalance}</p>
                    : plus && amount !== 0 && amount !== '' && amount < 500
                    ? <p className="alertText">{minInvest}</p>
                    : ''
                  }
                </div>
              }
            </div>
          </div>
        </div>
        <GasSelector />
        {
          invest && 
          <Accordion className="feesClass">
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="fees-header"
              id="fees-header"
              className={!plus ? 'hiddenPlus' : ''}
            >
              <p className="title">
                {
                  !plus && !withdrawValue ? <span>{gasText}: {anchorFeeInit} <span style={{textTransform:'uppercase'}}>{gasFeeName(selectGas.name)}</span></span> : feeText
                }
              </p>
            </AccordionSummary>
            {
              plus &&
              <AccordionDetails className="feesDetails">
                <p><span className="colorGray">{gasText}</span>: {plus && !withdrawValue && (deltaFeeInit === 0 ? '- -' : deltaFeeInit)} {gasFeeName(selectGas.name)}</p>
                <p className="space"><span className="colorGray">{strategyFeeText}</span>: 10%</p>
                <p className="space target">
                  <span className="colorGray">{targetText}</span><span>{width > 1199 ? ':' : ''}</span>
                  <Slider
                    sx={color}
                    className="slider"
                    onChange={handleCollateral}
                    aria-label="collateral"
                    value={collateral}
                    valueLabelDisplay="auto"
                    step={1}
                    min={Number(minCollateral)*100+50}
                    max={Number(minCollateral)*100+250}
                  />
                  {collateral}%
                </p>
                <p className="space">
                  <span className="colorGray">{reminder1}</span>
                  {title}
                  <span className="colorGray">{reminder2}</span>
                  <span style={color}>{increaseRatio.toFixed(0)}%</span>
                  <span className="colorGray">{reminder3}</span>
                  {defaultCol}%
                  <span className="colorGray">{reminder4}</span>
                </p>
                <p><span className="colorGray">{apyTitle}</span>: {parseNum(apyCal())}<span>%</span> <span className="colorGray">({apyText})</span></p>
              </AccordionDetails>
            }
          </Accordion>
        }
        {
          invest && amount > 0 && (plus ? maxDeltaInvest : maxInvest)-amount <= 1 &&
          <div className="warningMsg">
            {text.warning}
          </div>
        }
        {
          (checkGas === -1 ||
            (
              getGas.amount && (Number(getGas.amount) < (plus ? Number(deltaInvFee.amount) : Number(ancInvFee.amount)))
            )
          ) &&
          <div className="warningMsg">
            {text.feeError}
          </div>
        }
        <div className="confirmAsset">
          <button
            onClick={handleInvest}
            disabled={
              loading ?
              true :
              checkGas === -1 ?
              true :
              getGas.amount && (deltaFeeInit > getGas.amount/1000000) ?
              true :
              plus && !invest ?
              false :
              (plus && amount >= 500 && amount <= (invest ? balance : withdrawValue)) && amount <= 100000 ?
              false :
              (!plus && amount > (invest ? 0.000001 : 0) && amount <= (invest ? balance : withdrawValue)) ?
              false : true
            }
            className={
              checkGas === -1
              ? 'disabled' :
              getGas.amount && (deltaFeeInit > getGas.amount/1000000)
              ? 'disabled' :
              plus && !invest
              ? '' :
              (plus && amount >= 500 && amount <= (invest ? balance : withdrawValue)) && amount <= 100000
              ? '' :
              (!plus && amount > (invest ? 0.000001 : 0) && amount <= (invest ? balance : withdrawValue))
              ? '' : 'disabled'
            }
          >
            {
              success ? <Success /> : (
                loading ? <Spinner /> : (invest ? 'Invest' : 'Withdraw')
                )
            }
          </button>
        </div>
      </div>
    </div>
  )
}