import React from 'react'
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';

// Material
// import SearchIcon from '@mui/icons-material/Search';
import InputBase from '@mui/material/InputBase';

export default function Search(props) {

  const { language } = useApp();
  const text = textContext[language].investment.search;

  const [search, setSearch] = React.useState('');

  const handleEnter = (t) => {
    setSearch(t);
    props.handleSearch(t)
  };

  const handleClick = (t) => {
    if (t === search) {
      setSearch('');
      props.handleSearch('')
    } else {
      setSearch(t)
      props.handleSearch(t);
    };
  };

  const tokens = [
    { assets: 'mSQ' },
    { assets: 'mSPY' },
    { assets: 'mETH' },
    { assets: 'mGOOGL' },
  ];

  return (
    <div id="search">
      <div className="wrapper">
        <div className="searchWrapper">
        </div>
        <InputBase
          value={search}
          onChange={(event) => handleEnter(event.target.value)}
          placeholder={search === '' ? text.searchBar : ''}
          inputProps={{ 'aria-label': 'search' }}
        />
      </div>
      <div className="filterSearch">
        <p>{text.popular}</p>
        {
          tokens.map((t, i) => (
            <span
              className={t.assets.toLowerCase() === search.toLowerCase() ? 'active' : ''}
              onClick={() => handleClick(t.assets)} key={i}
            >{t.assets}</span>
          ))
        }
      </div>
    </div>
  )
}