import React from 'react'
import { Link } from "react-router-dom";
import { useToken } from '../../context/TokenContext';
import { useApp } from '../../context/AppContext';
import { useWallet } from '@terra-money/wallet-provider';
import Drawer from '@mui/material/Drawer';
import textContext from '../../textContext';

// Components
import NavLink from './NavLink';
import Footer from './Footer';
import Connect from './Connect';
import Pending from './Pending';
import AuthUser from './AuthUser';
import Theme from './Theme';

// Assets
import { logoText } from '../../assets/images';

export default function Navbar() {

  const { holderAddress, progress } = useToken();
  const { status, network } = useWallet();
  const { width } = useApp();  
  const [nav, setNav] = React.useState(false);
  const { language } = useApp();
  const text = textContext[language].navbar;

  // Toggle drawer
  const toggleDrawer = (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setNav(!nav);
  };

  const mobileNav = (
    <nav id="mobileNav">
      <div>
        <div className="logoClass">
          <Link to="/" className="linkClass">
            <img width="27" src={logoText} alt="aperture logo" /> <p>Aperture</p>
          </Link>
          { network.chainID === "bombay-12" && <span>{text.test}</span> }
        </div>
        <div className="flex">
          <div className="connect">
            { status === "WALLET_NOT_CONNECTED" && <Connect /> }
            { progress.length !== 0 && <Pending /> }
          </div>
          <button onClick={toggleDrawer} className="buttonBorder">
            {text.menu}
          </button>
          <Drawer className="drawerClass" anchor='right' open={nav} onClose={toggleDrawer}>
            <div>
              <div className="mobileAuth">
                { status === "WALLET_CONNECTED" && <AuthUser token={holderAddress} /> }
                { status === "WALLET_CONNECTED" && <hr /> }
              </div>
              <div onClick={toggleDrawer}>
                <NavLink />
              </div>
              <div style={{padding:'1rem 1rem 0'}}>
                <hr />
              </div>
              <Theme />
              <div onClick={toggleDrawer}>
                <Footer />
              </div>
            </div>
          </Drawer>
        </div>
      </div>
    </nav>
  );

  return (
    <React.Fragment>
      {
        width < 1200 ? mobileNav :
        <nav>
          <div>
            <div className="logoClass">
              <Link to="/" className="linkClass">
                <img src={logoText} alt="aperture logo" /> <p>Aperture</p>
              </Link>
              { network.chainID === "bombay-12" && <span>{text.test}</span> }
            </div>
            <div className="connect">
              { status === "WALLET_CONNECTED" ? <AuthUser token={holderAddress} /> : <Connect /> }
            </div>
            { progress.length !== 0 && <Pending /> }
            <hr />
            <NavLink />
            <Theme />
            <Footer />
          </div>
        </nav>
      }
    </React.Fragment>
  )
}