import React from 'react'
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useToken } from '../../context/TokenContext';

export default function Value(props) {

  const { language } = useApp();
  const text = textContext[language].portfolio.asset;
  const { exchange, parseNum } = useToken();

  const histoicalCalc = (ini, cur) => {
    let calc = parseNum(props.value - props.initial);
    let percent = parseNum((props.value - props.initial)/props.initial*100)
    return { calc, percent }
  };

  let formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
  });

  return (
    <div
      className="valueClass"
      style={
        props.anchor ?
        {
          justifyContent: 'flex-start',
          marginLeft:'1.75rem'
        }
        : {}
      }>
      <div>
        <span>{text.value}</span>
        {
          props.anchor && exchange ?
          <h2>${parseNum((Number(props.value)/1e6)*Number(exchange['exchange_rate']))}</h2>
          :
          <h2>${props.value && parseNum(props.value)}</h2>
        }
      </div>
      { props.position !== 'anc' && <hr /> }
      {
        props.position !== 'anc' &&
        <div>
          <span>{text.original}</span>
          <h3>${props.initial && parseNum(props.initial)}</h3>
          <span>{text.historical}</span>
          <h3>{props.value && props.initial && formatter.format(histoicalCalc().calc)} <span className="fontOrange">{props.value && props.initial && histoicalCalc().percent}%</span></h3>
        </div>
      }
    </div>
  )
}