import React from 'react'
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';

// Material
import SearchIcon from '@mui/icons-material/Search';
import InputBase from '@mui/material/InputBase';
import CancelIcon from '@mui/icons-material/Cancel';
import AddIcon from '@mui/icons-material/Add';

export default function AssetSearch(props) {

  const { language } = useApp();
  const text = textContext[language].investment.search;

  const [search, setSearch] = React.useState('');
  const [list, setList] = React.useState(false);
  const [tokenList, setTokenList] = React.useState([]);

  const handleEnter = (event) => {
    if (event.key === 'Enter' && !tokenList.includes(search) && !props.plus && search === 'ust') {
      props.setToken(search);
      props.close();
    }
  };

  const handleSearch = (event) => {
    if (!tokenList.includes(search) && search !== '' && !props.plus && search === 'ust') {
      props.setToken(search);
      props.close();
    }
  };

  const handleClick = (t) => {;
    if (!tokenList.includes(t)) {
      props.setToken(t);
      props.close();
    }
  };

  const handleRemove = (t) => {
    let newList = tokenList.filter(f => f !== t);
    setTokenList(newList);
  }

  const handleList = () => {
    setList(true)
  }

  const clearTokens = () => setTokenList([]);

  const tokens =  [ {token:'ust'} ];

  const closeClass = {
    fontSize: 16,
    margin: '0 .75rem',
    color: '#777E90',
  }

  return (
    <div id="search">
      <div className="wrapper">
        <div className="tokenSearch">
          {
            tokenList.map((t,i) => (
              <span onClick={() => handleRemove(t)} key={i}>
                <p>{t}</p> <span>&#x2715;</span>
              </span>
            ))
          }
        </div>
        <div className="searchWrapper">
          {tokenList.length > 0 && <CancelIcon onClick={clearTokens} sx={closeClass} />}
          <SearchIcon onClick={handleSearch} />
        </div>
        <InputBase
          value={search}
          onClick={handleList}
          onChange={(event) => setSearch(event.target.value)}
          onKeyDown={handleEnter}
          placeholder={tokenList.length < 1 ? text.searchBar : ''}
          inputProps={{ 'aria-label': 'search' }}
        />
      </div>
      {
        !list &&
        <div className="filterSearch">
          <p>{text.popular}</p>
          {
            tokens.map((t, i) => (
              <span onClick={() => handleClick(t.token)} key={i}>{t.token}</span>
            ))
          }
        </div>
      }
      <hr />
      <span className="manageClass"><AddIcon /><p>{text.manage}</p></span>
    </div>
  )
}